.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-logo {
  width: 0.98em;
  height: 1em;
  fill: initial;
}

body {
  font-size: 18px;
  background: #FFF;
  line-height: 1.4;
}

.block {
  display: flex;
}

.button {
  font-size: 18px;
  color: #fff;
  border-radius: 20px;
  padding: 22px 68px;
}

.button--border-middle {
  background-color: #fff;
  color: #1d354e;
  padding: 10px 25px;
  font-size: 16px;
}

.button--border-middle:hover {
  background: rgba(255, 255, 255, 0.7);
}

.header {
  font-family: "Montserrat", Helvetica, sans-serif;
  color: #fff;
  background-color: #00bdca;
  padding: 64px 0 115px;
}

.header__inner {
  font-size: 0;
  margin: auto;
  max-width: 996px;
  display: flex;
  justify-content: space-between;
}

.header__menu {
  display: flex;
  align-items: center;
}

.header__login {
  margin: 0 8px;
  text-decoration: none;
  font-weight: 600;
  font-size: 16px;
}

.logo {
  font-family: "Montserrat", Helvetica, sans-serif;
  display: flex;
}

.logo__image {
  display: flex;
  align-items: center;
}

.logo__image .icon {
  width: 43px;
  height: 44px;
  fill: #fff;
}

.logo__text {
  margin-left: 12px;
}

.logo__title {
  font-weight: 600;
  font-size: 24px;
}

.logo__title--cyan {
  color: #56ecf6;
}

.logo__description {
  line-height: 0.5;
  font-weight: 400;
  font-size: 13px;
}

.nav {
  font-family: "Montserrat", Helvetica, sans-serif;
}

.nav__list {
  display: flex;
}

.nav__item {
  margin: 0 17px;
}

.nav__item:last-child {
  margin-right: 25px;
}

.nav__link {
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  font-size: 16px;
}

.nav__link:hover {
  text-decoration: underline;
}

.nav__link:active {
  color: rgba(255, 255, 255, 0.7);
}

.lang {
  font-family: "Montserrat", Helvetica, sans-serif;
  position: relative;
}

.lang__current {
  padding: 10px 24px;
  font-size: 16px;
  color: transparent;
}

.lang__list {
  padding: 10px 25px;
  background-color: #fff;
  border-radius: 20px;
  left: 0;
  top: 0;
  position: absolute;
}

.lang__list:hover .lang__item--hidden {
  display: block;
}

.lang__item {
  font-size: 16px;
}

.lang__item:hover {
  text-decoration: underline;
}

.lang__item--hidden {
  display: none;
}

.lang__link {
  text-decoration: none;
  color: #1d354e;
  font-weight: 600;
  font-size: 16px;
}
